import { Component } from '@angular/core';
import { NavController, Platform, AlertController } from 'ionic-angular';
import { SpeechRecognition } from '@ionic-native/speech-recognition';
import { Observable } from 'rxjs/Observable';
import { ChangeDetectorRef } from '@angular/core';
import { Storage } from '@ionic/storage';

//declare var SpeechRecognition: any;
declare var window: any;

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
    
    public properties: any;
    public notes: Array<any>;
    public languages: Array<string>;
    public recognitionObject: any;

    constructor(public navCtrl: NavController, private speechRecognition: SpeechRecognition, private plt: Platform, private cd: ChangeDetectorRef, private storage: Storage, private alertCtrl: AlertController) {
        this.properties = {isRecording:false, language: "en-US", isIos: this.plt.is('ios'), currentMatch:""};
        this.notes = [];
        this.languages = [];
        
        
        this.storage.get("notes").then((data) => {
            if (data){
                this.notes = data;
            }
        });
        
        this.getSupportedLanguages();
        
        
        this.plt.ready().then(() => {
            
            if(window.SpeechRecognition){
                this.setupRecognition();
            }
            
                
            
            
           
        })
        
    }
    
    private setupRecognition(){
        this.recognitionObject = new window.SpeechRecognition(); 
        
        this.recognitionObject.continuous = true;
        this.recognitionObject.lang = this.properties.language;
        this.recognitionObject.maxAlternatives = 3;

        this.recognitionObject.onnomatch = (event => {
            console.log('No match found.');
        });  
        
        
        this.recognitionObject.onstart = (event => {
            console.log('Started recognition.');
            this.properties.isRecording = true;
            this.properties.currentMatch = "";
        });  
        
        this.recognitionObject.onend = (event => {
            console.log('Stopped recognition.');
            if (this.properties.isRecording){
                this.recognitionObject.start();
            }
            else{
                this.notes.push(this.properties.currentMatch);
                this.storage.set("notes", this.notes);                 
            }
        });
        
        
        this.recognitionObject.onerror = (event => {
            console.log('Error...' + event.error);
            if (!this.properties.isRecording && this.properties.currentMatch){
                this.notes.push(this.properties.currentMatch);
                this.storage.set("notes", this.notes);                 
            }
        });     
        
        
        this.recognitionObject.onresult = (event => {
            if (event.results) {
                let result = event.results[0];
                this.properties.currentMatch = this.properties.currentMatch ? this.properties.currentMatch + " " + result : result;

            }
        });                       
               
    }

    
    public toggleListening(){
        if (this.properties.isRecording){
            this.properties.isRecording = false;
        }
        else{
            this.properties.isRecording = true;
            this.recognitionObject.start();            
        }
    }

    
    private getSupportedLanguages(){
        this.speechRecognition.getSupportedLanguages()
          .then(
            (languages: Array<string>) => {this.languages = languages;}
          ) 
          .catch(() => {
              
          })       
    }   
    
    public changeLanguage(){
        let data = {
            title: "Change Language",
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel'
                },
                {
                    text: 'OK',
                    handler: data => {
                       console.log(data);
                       this.properties.language = data;
                       this.recognitionObject.lang = this.properties.language;
                   }
                }
            ],
            inputs: []
        }
        
        for (let language of this.languages){
            data.inputs.push({type: 'radio', label: language, value: language, checked:language === this.properties.language})
        }
        
        let alert = this.alertCtrl.create(data);
        alert.present();        
    }


}



